.page_manifesto,
.page_tangenze {
  background-color: #000;
}
@media (min-width: 751px) and (max-width: 970px) {
  .page {
    font-size: 36px;
    line-height: 36px;
  }
  .page svg {
    width: 105px;
    margin: 13px 0 36px 35px;
  }
  .page h1 {
    font-size: 36px;
    line-height: 36px;
    padding: 0 33px;
    margin: 0 0 72px;
  }
  .page h3,
  .page p {
    font-size: 36px;
    line-height: 36px;
    padding: 0 33px;
    margin: 0 0 36px;
  }
  .page a,
  .page p a {
    background-size: 100% 2px;
  }
}
@media (max-width: 750px) {
  .page {
    font-size: 24px;
    line-height: 24px;
  }
  .page svg {
    width: 72px;
    margin: 13px 0 24px 25px;
  }
  .page h1 {
    margin: 0 0 48px;
  }
  .page h1,
  .page h3 {
    max-width: 628px;
    padding: 0 24px;
    font-size: 24px;
    line-height: 24px;
  }
  .page h3,
  .page p {
    margin: 0 0 24px;
  }
  .page p {
    padding: 0 24px;
    max-width: 628px;
  }
  .page p .caption,
  .page p picture img {
    margin: -60px 0 60px;
  }
  .page p picture:first-of-type img {
    margin: 60px 0;
  }
  .page a,
  .page p a {
    background-size: 100% 1px;
  }
  .page .videocontrol {
    padding: 0;
  }
  .page .videocontrol video {
    margin: 60px 0;
  }
  .page .videocontrol .modifyaudio {
    padding-left: 24px;
    margin-top: -60px;
  }
}
#evento_form {
  position: relative;
}
#evento_form .box {
  max-width: 600px;
  width: 100%;
  position: relative;
  padding: 0 24px;
  margin: 100px auto 0;
}
#evento_form .box svg {
  width: 140px;
  margin: 0 0 0 3px;
  transition: all 50ms ease-in-out;
}
#evento_form .box h1 {
  font-family: FTSwitch-Regular, FTSwitch, Helvetica, Arial, sans-serif;
  font-size: 48px;
  line-height: 48px;
  font-weight: 400;
  padding: 40px 0 96px;
  color: #fff;
  border-bottom: 1px solid #fff;
  width: 100%;
  margin: 0 0 48px;
}
#evento_form .box h2 {
  padding: 38px 0 370px;
}
#evento_form .box h2,
#evento_form .box h3 {
  font-family: FTSwitch-Regular, FTSwitch, Helvetica, Arial, sans-serif;
  font-size: 48px;
  line-height: 48px;
  font-weight: 400;
  margin: 0;
  color: #fff;
  width: 100%;
}
#evento_form .box h3 {
  padding: 40px 0;
}
#evento_form .box h3 a {
  color: #fff;
}
#evento_form .box p {
  color: #fff;
  margin: 0;
  padding: 0 0 23px;
  font-size: 18px;
  line-height: 23px;
}
#evento_form .box p a {
  color: #fff;
}
#evento_form .box form input {
  width: 100%;
  height: 60px;
  padding: 0 0 0 15px;
  margin: 0 0 10px;
  font-size: 20px;
  line-height: 60px;
  text-align: left;
  font-family: UntitledSansWeb, UntitledSans, Helvetica, Arial, sans-serif;
}
#evento_form .box form input[type="submit"] {
  margin: 30px 0 40px;
  padding: 0 0 0 15px;
  height: 60px;
  background-color: #bdbdbd;
  border-color: #e6e6e6 #000 #000 #e6e6e6;
  border-style: solid;
  border-width: 2px;
  box-sizing: border-box;
  cursor: pointer;
  line-height: 56px;
  max-width: 390px;
}
#evento_form .box .images {
  margin: 25px 0 48px;
}
#evento_form .box .images img {
  image-rendering: auto;
  -moz-image-rendering: auto;
  max-width: 600px;
  width: 100%;
}
#evento_form .box .images img.main {
  max-width: 800px;
  width: auto;
}
#evento_form .box .group_images {
  height: 534px;
  width: 100vw;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  margin-left: calc((100vw - 552px) / -2);
  padding: 0 calc((100vw - 552px) / 2);
}
#evento_form .box .group_images img {
  margin-right: 12px;
}
#evento_form.inaugurazione,
.page_inaugurazione {
  background-color: #000;
}
@media (prefers-color-scheme: dark) {
  #root,
  html {
    background-color: #1e1e1e;
    color: #dcdcdc;
  }
  #error h1 img,
  #error h2 img,
  #error h3 img,
  #error li img,
  #error p img,
  #home h1 img,
  #home h2 img,
  #home h3 img,
  #home li img,
  #home p img {
    display: none;
  }
  #error h1 #logowhite,
  #error h2 #logowhite,
  #error h3 #logowhite,
  #error li #logowhite,
  #error p #logowhite,
  #home h1 #logowhite,
  #home h2 #logowhite,
  #home h3 #logowhite,
  #home li #logowhite,
  #home p #logowhite {
    display: inline-block;
  }
  #error h1 a,
  #error h2 a,
  #error h3 a,
  #error li a,
  #error p a,
  #home h1 a,
  #home h2 a,
  #home h3 a,
  #home li a,
  #home p a {
    color: #689bfb;
  }
  #error h1 a:after,
  #error h2 a:after,
  #error h3 a:after,
  #error li a:after,
  #error p a:after,
  #home h1 a:after,
  #home h2 a:after,
  #home h3 a:after,
  #home li a:after,
  #home p a:after {
    background-color: #689bfb;
  }
  #error h1 a:visited,
  #error h2 a:visited,
  #error h3 a:visited,
  #error li a:visited,
  #error p a:visited,
  #home h1 a:visited,
  #home h2 a:visited,
  #home h3 a:visited,
  #home li a:visited,
  #home p a:visited {
    color: #689bfb;
  }
  #info_home a {
    color: #dcdcdc;
  }
  #article .hr {
    border-bottom: 1px solid #dcdcdc;
  }
  #article p .mark {
    background-image: linear-gradient(120deg, #706444, #706444);
  }
  #article a {
    color: #689bfb;
    background-image: linear-gradient(120deg, #689bfb, #689bfb);
  }
  #log h1 img,
  #log h2 img {
    display: none;
  }
  #log h1 #logowhite,
  #log h2 #logowhite {
    display: inline-block;
  }
  #log h1:after {
    background-color: #689bfb;
  }
}
